.btn {
  font-family: "Fira Sans", OpenSans, sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 0.02em;
  color: $black;
  text-decoration: none;
  background-color: $goldenrod;
  border: 2px solid transparent;
  border-radius: 50px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 15px;
  cursor: pointer;
  width: 100%;
  transition: all .3s ease;
  @include response(767px) {
    padding: 10px;
  }
}
.btn-default {
  @include hover() {
    background-color: #ff971d;
  }
}
.btn-primary {
  border: 2px solid $chambray;
  background-color: $chambray;
  color: $white;
  @include hover() {
    border: 2px solid #5366f4;
    background-color: #5366f4;
  }
}