.hero {
  background-color: $chambray;
  height: calc(100vh - 71px);
  display: flex;
  align-items: center;
  @include response(1249px) {
    height: auto;
    padding: 54px 0;
  }
  @include response(1023px) {
    padding: 44px 0;
  }
  &__holder {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    @include response(767px) {
      flex-direction: column-reverse;
      align-items: center;
    }
  }
  &__pic {
    max-width: 365px;
    width: 100%;
    flex: 1 1 365px;
    margin-right: 35px;
    @include response(767px) {
      margin: 0;
      flex: 1 1 auto;
    }
  }
  &__col {
    max-width: 324px;
    width: 100%;
    flex: 1 1 324px;
    @include response(767px) {
      margin-bottom: 34px;
    }
    .btn {
      max-width: 270px;
      flex: 1 1 270px;
      margin-bottom: 24px;
    }
  }
  &__title {
    font-size: 30px;
    line-height: 36px;
    font-weight: bold;
    color: $white;
    margin-bottom: 16px;
    span {
      color: $goldenrod;
    }
  }
  &__desc {
    margin-bottom: 24px;
    font-size: 18px;
    line-height: 28px;
    font-weight: 400;
    color: $white;
    span {
      color: $goldenrod;
    }
  }
}

.agreement {
  font-size: 12px;
  line-height: 19px;
  color: #f6f6f7;
  a {
    color: #f6f6f7;
    text-decoration: underline;
    @include hover() {
      text-decoration: none;
    }
  }
}

.intro {
  padding: 100px 0 86px;
  background-color: #f6f6f7;
  @include response(479px) {
    padding: 74px 0;
  }
  &__holder {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    @include response(1023px) {
      flex-direction: column;
      align-items: center;
    }
  }
  .title {
    max-width: 386px;
    width: 100%;
    margin: 0 auto 20px;
    text-align: center;
  }
  .subtitle {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: $bunting;
    max-width: 570px;
    width: 100%;
    margin: 0 auto 34px;
    p {
      margin: 0;
    }
  }
}

.tools-block {
  padding: 0 10px;
  max-width: calc(50% - 20px);
  width: 100%;
  @include response(1023px) {
    max-width: calc(75% - 20px);
    &:first-of-type {
      margin-bottom: 45px;
    }
  }
  @include response(767px) {
    max-width: 100%;
    padding: 0;
    &:first-of-type {
      margin-bottom: 24px;
    }
  }
  &__list {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    @include response(479px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__items {
    max-width: calc(33.3333% - 30px);
    width: 100%;
    margin: 0 15px;
    @include response(767px) {
      max-width: calc(33.3333% - 20px);
      margin: 0 10px;
    }
    @include response(479px) {
      max-width: 100%;
      margin-bottom: 24px;
    }
  }
  &__img {
    margin-bottom: 8px;
    text-align: center;
  }
  &__title {
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    text-align: left;
    color: $bunting;
    max-width: 170px;
    width: 100%;
    margin: 0 auto 20px;
    @include response(479px) {
      text-align: center;
      margin: 0 auto;
      max-width: 100%;
    }
    &--center {
      text-align: center;
    }
  }
  &__subtitle {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    max-width: 170px;
    width: 100%;
    text-align: left;
    margin: 0 auto;
    word-break: break-word;
    @include response(479px) {
      max-width: 100%;
      text-align: center;
    }
  }
  .line {
    max-width: 400px;
    width: 100%;
    flex: 1 1 400px;
    height: 1px;
    background-color: $chambray;
    margin: 0 auto 56px;
    position: relative;
    @include response(479px) {
      display: none;
    }
    &::after,
    &::before {
      content: '';
      display: block;
      position: absolute;
      height: 20px;
      width: 1px;
      background-color: $chambray;
      top: 0;
    }
    &::after {
      right: 0;
    }
    &::before {
      left: 0;
    }
  }
  .title {
    position: relative;
    margin: 0 auto 52px;
    @include response(479px) {
      margin: 0 auto 32px;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 1px;
      height: 50px;
      background-color: $chambray;
      left: 50%;
      top: 48px;
      transform: translateX(-1px);
      @include response(479px) {
        display: none;
      }
    }
  }
}

.product {
  padding: 100px 0;
  @include response(767px) {
    padding: 74px 0;
  }
  &__holder {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    @include response(767px) {
      flex-direction: column;
      align-items: center;
      max-width: 280px;
      width: 100%;
      margin: 0 auto;
    }
  }
  &__col {
    max-width: 33.3333%;
    flex: 1 1 33.3333%;
    width: 100%;
    &--left {
      padding-top: 34px;
      @include response(767px) {
        order: 3;
        max-width: 260px;
        width: 100%;
        margin-left: auto;
        padding: 0;
      }
    }
    &--center {
      max-width: 370px;
      flex: 1 1 370px;
      @include response(1023px) {
        max-width: 280px;
        flex: 1 1 280px;
      }
      @include response(767px) {
        order: 1;
      }
    }
    &--right {
      padding-top: 34px;
      @include response(767px) {
        order: 2;
        max-width: 260px;
        width: 100%;
        margin-left: auto;
        padding: 0;
      }
    }
  }
  &__figure {
    border: 1px solid $chambray;
    border-radius: 10px;
    margin: 0;
    height: 360px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include response(767px) {
      height: 280px;
    }
  }
  &__pic {
    text-align: center;
    margin: 0 0 24px;
  }
  .title {
    margin-bottom: 30px;
  }
}

.service {
  &__list {
    margin-bottom: 10px;
  }
  &__items {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    &::after {
      content: '';
      display: block;
      position: absolute;
      background: url(../images/star.svg) no-repeat center / auto;
      height: 16px;
      width: 16px;
      top: 4px;
      opacity: 0.7;
    }
  }
  &--left {
    border-bottom: 1px solid $chambray;
    @include response(767px) {
      border-left: 1px solid #39459d;
      padding-top: 34px;
    }
    &:first-of-type {
      margin-bottom: 52px;
      @include response(1023px) {
        margin-bottom: 26px;
      }
      @include response(767px) {
        margin: 0;
      }
    }
    .title {
      padding-right: 48px;
      text-align: right;
      @include response(1023px) {
        padding-right: 28px;
      }
      @include response(767px) {
        text-align: left;
        padding: 0 0 0 28px;
      }
    }
    .service__items {
      justify-content: flex-end;
      padding-right: 48px;
      text-align: right;
      @include response(1023px) {
        padding-right: 28px;
      }
      @include response(767px) {
        justify-content: flex-start;
        padding: 0 0 0 48px;
        text-align: left;
      }
      &::after {
        right: 24px;
        @include response(1023px) {
          right: 6px;
        }
        @include response(767px) {
          right: unset;
          left: 24px;
        }
      }
    }
  }
  &--center {
    border-left: 1px solid $chambray;
    border-bottom: 1px solid $chambray;
    max-width: 300px;
    width: 100%;
    margin: 0 0 0 auto;
    padding-top: 48px;
    @include response(1023px) {
      max-width: 244px;
    }
    @include response(767px) {
      max-width: 260px;
      padding-top: 34px;
    }
    .title {
      text-align: left;
      padding-left: 48px;
    }
    .service__items {
      padding-left: 48px;
      &::after {
        left: 24px;
      }
    }
    .service__list {
      padding-bottom: 16px;
    }
  }
  &--right {
    border-bottom: 1px solid $chambray;
    @include response(767px) {
      border-left: 1px solid #39459d;
      padding-top: 34px;
    }
    &:first-of-type {
      margin-bottom: 52px;
      @include response(1023px) {
        margin-bottom: 26px;
      }
      @include response(767px) {
        margin: 0;
      }
    } 
    .title  {
      padding-left: 48px;
      text-align: left;
      @include response(1023px) {
        padding-left: 28px;
      }
    }
    .service__items {
      justify-content: flex-start;
      padding-left: 48px;
      @include response(1023px) {
        padding-left: 28px;
      }
      @include response(767px) {
        padding-left: 48px;
      }
      &::after {
        left: 24px;
        @include response(1023px) {
          left: 6px;
        }
        @include response(767px) {
          left: 24px;
        }
      }
    }
  }
  .title {
    margin-bottom: 6px;
  }
}

.process {
  background-color: $chambray;
  padding: 152px 0 0;
  @include response(1023px) {
    padding: 100px 0 0;
  }
  @include response(767px) {
    padding: 74px 0 0;
  }
  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 140px;
    @include response(767px) {
      flex-direction: column;
      margin-bottom: 74px;
    }
    @include response(479px) {
      margin-bottom: 44px;
    }
  }
  &__pic {
    padding-right: 33px;
    @include response(767px) {
      padding: 0 0 64px;
    }
    @include response(479px) {
      padding: 0 0 44px;
    }
  }
  &__block {
    .title {
      margin-bottom: 38px;
      text-align: left;
      @include response(767px) {
        margin-bottom: 24px;
      }
    }
  }
  &__list {}
  &__items {
    color: $white;
  }
}

.request {
  background: $bunting url(../data/request-bg.png) repeat-y center/ auto;
  background-attachment: fixed;
  padding: 32px 15px;
  &__holder {}
  &__desc {
    font-size: 30px;
    line-height: 36px;
    font-weight: bold;
    text-align: center;
    color: $blizzard;
    margin-bottom: 24px;
    span {
      color: $goldenrod;
    }
  }
  .btn {
    max-width: 370px;
    margin: 0 auto;
  }
}

.cases {
  padding: 100px 0;
  @include response(767px) {
    padding: 74px 0;
  }
  &__list {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-bottom: 52px;
    @include response(1023px) {
      margin: 0 -10px 38px;
      flex-wrap: wrap;
    }
    @include response(767px) {
      justify-content: flex-start;
    }
    @include response(479px) {
      flex-direction: column;
      margin: 0 0 38px;
    }
  }
  &__items {
    max-width: calc(25% - 30px);
    flex: 1 1 calc(25% -  30px);
    width: 100%;
    margin: 0 15px;
    @include response(1023px) {
      max-width: calc(25% - 20px);
      flex: 1 1 calc(25% -  20px);
      margin: 0 10px;
    }
    @include response(767px) {
      max-width: calc(50% - 20px);
      flex: 1 1 calc(50% -  20px);
      margin: 0 10px 34px;
      flex-wrap: wrap;
    }
    @include response(479px) {
      max-width: 100%;
      flex: 1 1 auto;
      margin: 0 0 34px;
    }
    &:last-of-type {
      margin-bottom: 0;
    }
  }
  &__link {
    display: block;
    @include hover() {
      .cases__pic {
        &::after {
          background-color: rgba(0, 0, 0, 0.3);
        } 
      }
    }
  }
  &__pic {
    border-radius: 10px;
    border: 1px solid #c7c7c7;
    overflow: hidden;
    transition: 0.35s ease-in-out;
    margin-bottom: 30px;
    position: relative;
    overflow: hidden;
    @include response(1023px) {
      margin-bottom: 18px;
    }
    @include response(767px) {
      > img {
        width: 100%;
      }
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      background-color: transparent;
      width: 100%;
      height: 100%;
      transition: 0.35s ease-in-out;
    }
  }
  &__title {
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    color: $bunting;
    transition: 0.35s ease-in-out;
    opacity: 1;
    @include hover() {
      opacity: 0.7;
    }
  }
  &__subtitle {
    color: $chambray;
    display: block;
    margin-bottom: 8px;
  }
  &__info {
    text-align: center;
  }
  &__more {
    font-size: 18px;
    line-height: 28px;
    font-weight: normal;
    color: $chambray;
    display: inline-block;
    margin-bottom: 12px;
    position: relative;
    &::after {
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0;
      height: 1px;
      background-color: $chambray;
      transition: 0.35s ease-in-out;
    }
    @include hover() {
      &::after {
        width: 100%;
      }
    }
  }
  .btn {
    max-width: 370px;
    margin: 0 auto;
  }
  .title {
    margin-bottom: 50px;
    @include response(767px) {
      margin-bottom: 34px;
    }
  }
}

.team {
  background-color: #f6f6f7;
  padding: 100px 0 75px;
  @include response(767px) {
    padding: 74px 0;
  }
  &__holder {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    max-width: 555px;
    margin: 0 auto;
  }
  &__block {
    margin: 0 0 40px;
    max-width: 25%;
    width: 100%;
    @include response(767px) {
      margin: 0 8px 40px;
    }
    @include response(479px) {
      max-width: calc(50% - 18px)
    }
    &:last-of-type {
      @include response(479px) {
        margin: 0 8px 0;
      }
    }
  }
  &__pic {
    width: 110px;
    height: 110px;
    border: 1px solid $onahau;
    border-radius: 50%;
    margin: 0 auto 14px;
    background-color: $white;
  }
  &__position {
    text-align: center;
  }
  .title {
    margin-bottom: 18px;
  }
  .subtitle {
    text-align: center;
    margin-bottom: 54px;
    @include response(767px) {
      margin-bottom: 38px;
    }
  }
}

.callback {
  background-color: $chambray;
  padding: 115px 0;
  @include response(1023px) {
    padding: 100px 0;
  }
  @include response(767px) {
    padding: 74px 0;
  }
  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__pic {
    padding-right: 148px;
    @include response(767px) {
      padding: 0 0 34px;
    }
  }
}

.form {
  &__holder {}
  &__title {
    color: $white;
    max-width: 580px;
    width: 100%;
    margin: 0 auto 24px;
    .yellow {
      color: $goldenrod;
    }
    .blue {
      color: $onahau;
    }
  }
  &__block {
    max-width: 370px;
    flex: 1 1 370px;
    width: 100%;
    margin: 0 auto;
    .agreement {
      font-size: 16px;
    }
  }
  &__subtitle {
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;
    color: $goldenrod;
    margin-bottom: 8px;
    text-align: left;
  }
  &__row {
    position: relative;
    margin-bottom: 10px;
    p.error {
      font-size: 18px;
      line-height: 28px;
      display: flex;
      align-items: center;
      text-align: center;
      color: #ff0000;
      position: absolute;
      top: 50%;
      left: 50%;
      margin: 0;
      background-color: $white;
      transform: translate(-50%, -50%);
      transition: 0.3s ease-in-out;
      opacity: 0;
      visibility: hidden;
      z-index: -1;
      &.active {
        opacity: 1;
        visibility: visible;
        z-index: 1;
      }
    }
  }
  
}

.popup-modal {
  position: fixed;
  width: 100%;
  height: calc(100% + 71px);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 777;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(23, 21, 80, 0.5);
  transition: 0.45s ease-in-out;
  opacity: 0;
  visibility: hidden;
  &.active {
    opacity: 1;
    visibility: visible;
    .popup-modal__holder {
      transform: scale(1);
    }
  }
  &__holder {
    max-width: 770px;
    width: 100%;
    margin: 0 auto;
    border-radius: 10px;
    background-color: $chambray;
    padding: 10px 10px 50px 10px;
    transform: scale(0);
    transition: 0.45s ease-in-out;
    position: relative;
    @include response(767px) {
      height: calc(100vh - 72px);
      overflow: scroll;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
  .title {
    @include response(767px) {
      font-size: 24px;
      line-height: 30px;
      margin: 0 auto 16px;
    }
  }
  .form__holder {
    @include response(479px) {
      padding: 34px 0;
      height: 100%;
    }
  }
}

.close {
  width: 25px;
  height: 25px;
  border: none;
  background-color: transparent;
  padding: 0;
  display: block;
  margin: 0 0 0 auto;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  @include hover() {
    &::after,
    &::before {
      background-color: $goldenrod;
    }  
  }
  &::after,
  &::before {
    content: '';
    display: block;
    position: absolute;
    background-color: $white;
    width: 100%;
    height: 3px;
    transition: .3s ease-in-out;
  }
  &::after {
    transform: rotate(45deg);
  }
  &::before {
    transform: rotate(-45deg);
  }
}