.header {
	background-color: $bunting;
	padding: 10px 0;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	z-index: 999;
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__holder {
		display: flex;
		justify-content: space-between;
		align-items: center;
		transition: 0.35s ease-in-out;
		@include response(767px) {
			position: fixed;
			top: 72px;
			right: 0;
			left: 0;
			width: 100%;
			height: calc(100vh - 72px);
			width: 85%;
			margin-left: auto;
			background-color: $bunting;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			padding: 25px;
			overflow-y: scroll;
			transform: translateX(120%);
			opacity: 0;
			visibility: hidden;
		}
		&.open-menu {
			opacity: 1;
			visibility: visible;
			transform: translateX(0);
			&::after {
				content: '';
				display: block;
				position: absolute;
				background-color: #000;
			}
		}
	}
}

.logo {
	@include response(1023px) {
		max-width: 244px;
		width: 100%;
	}
	@include response(767px) {
		max-width: 184px;
	}
}

.nav {
	margin-right: 43px;
	@include response(767px) {
		margin: 0 0 94px;
		width: 100%;
		text-align: center;
	}
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		display: flex;
		justify-content: center;
		align-items: center;
		@include response(767px) {
			flex-direction: column;
		}
		li {
			margin: 0 9px;
			a {
				font-size: 16px;
				line-height: 24px;
				color: $white;
				font-weight: 400;
				padding: 4px 0;
				display: inline-block;
				position: relative;
				&::after {
					content: '';
					display: block;
					height: 1px;
					width: 100%;
					background-color: $white;
					position: absolute;
					bottom: 5px;
					left: 0;
					transition: 0.35s ease-in-out;
				}
				@include hover() {
					&::after {
						width: 0;
					}
				}
				@include response(767px) {
					display: block;
				}
			}
		}
	}
}

.contacts {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	@include response(767px) {
		align-items: center;
	}
	&__phone {
		font-size: 18px;
		line-height: 26px;
		font-weight: bold;
		color: $onahau;
		@include hover() {
			color: $white
		}
	}
	&__email {
		font-size: 16px;
		line-height: 24px;
		color: $white;
		font-weight: 400;
		position: relative;
		@include response(767px) {
			font-weight: bold;	
			color: $onahau;
		}
		&::after {
			content: '';
			display: block;
			height: 1px;
			width: 100%;
			background-color: #fff;
			position: absolute;
			bottom: 0;
			left: 0;
			transition: 0.35s ease-in-out;
			@include response(767px) {
				display: none;
			}
		}
		@include hover() {
			color: $onahau;
			&::after {
				width: 0;				
			}
		}
		&--mob {
			display: none;
			@include response(767px) {
				font-size: 16px;
				line-height: 24px;
				text-align: center;
				text-decoration-line: underline;
				color: $white;
				display: block;
			}
		}
	}
}

.burger {
	padding: 15px 5px;
	display: none;
	cursor: pointer;
	@include response(767px) {
		display: block;
	}
	&__holder {
		height: 100px;
		width: 25px;
		height: 22px;
		position: relative;
		&.active {
			span {
				&:first-of-type {
					transform: rotate(45deg);
				}
				&:nth-of-type(2) {
					opacity: 0;
					visibility: hidden;
				}
				&:last-of-type {
					transform: rotate(-45deg);
				}
			}
		}
		span {
			display: block;
			width: 100%;
			height: 3px;
			background-color: $white;
			transform: rotate(0);
			transition: 0.3s ease-in-out;
			position: absolute;
			&:first-of-type {
				top: 0;
			}
			&:nth-of-type(2) {
				top: 9px;
			}
			&:last-of-type {
				top: 18px;
			}
		}
	}
}