body {
  height: 100%;
  font-family: "Fira Sans", OpenSans, sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: $black;
}
* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
svg {
  transform: translateZ(0);
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type="submit"] {
  cursor: pointer;
}
h1 {
  font-size: 34px;
  line-height: 36px;
  font-weight: 700;
  margin: 0;
}
h2 {
  font-size: 24px;
  line-height: 26px;
  font-weight: normal;
  margin: 0;
}
h3 {
  font-size: 22px;
  line-height: 24px;
  font-weight: normal;
  margin: 0;
}
h4 {
  font-size: 16px;
  line-height: 18px;
  font-weight: normal;
}
h5 {
  font-size: 12px;
  line-height: 14px;
  font-weight: normal;
}
h6 {
  font-size: 10px;
  line-height: 12px;
  font-weight: normal;
}
p.error {
  font-size: 12px;
  line-height: 14px;
  color: red;
}
.wrapper {
  overflow: hidden;
  min-height: 100vh;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 71px;
}
.holder-block {
  @include response(767px) {
    overflow: hidden;
    position: relative;
    height: 100%;
  }
}
.main {
  flex-grow: 1;
}
.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
a {
  text-decoration: none;
  transition: 0.3s ease-in;
  color: $black;
}
.title {
  font-size: 30px;
  line-height: 36px;
  font-weight: bold;
  text-align: center;
  color: $chambray;
  &--sm {
    font-size: 22px;
    line-height: 26px;
  }
  &--xs {
    font-size: 18px;
    line-height: 26px;
    color: $bunting;
  }
  &--wh {
    color: $white;
  }
}
// Form styles 
.form__control{
  font-family: "Fira Sans", OpenSans, sans-serif;
  display: inline-block;
  vertical-align: top;
  width: 100%;
  height: 50px;
  font-size: 18px;
  line-height: 28px;
  font-weight: 400;
  padding: 14px 20px;
  color: $bunting;
  background-color: $white;
  border: 1px solid #E0E6ED;
  border-radius: 5px;
  outline: none;
  resize: none;
  margin: 0;
  transition: all .2s;
  text-align: center;
  &:focus {
    border: 1px solid $goldenrod;
  }
}
*::-webkit-input-placeholder {
  opacity: 1;
  color: #c7c7c7; 
}
*:-moz-placeholder {
  opacity: 1;
  color: #c7c7c7; 
}
*::-moz-placeholder {
  opacity: 1;
  color: #c7c7c7; 
}
*:-ms-input-placeholder {
  opacity: 1;
  color: #c7c7c7; 
}