.popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  display: none;
  &.active {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1;
  }
  &__holder {
    background: #fff;
    padding: 0;
    position: relative;    
    z-index: 999;
    overflow: hidden;
  }
  &__close {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__content {}
}

.iosBugFixCaret {
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden;
}