.footer {
  background-color: $bunting;
  padding: 100px 0 124px;
  @include response(1023px) {
    padding: 74px 0 100px;
  }
  @include response(767px) {
    padding: 44px 0 50px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__holder {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include response(767px) {
      flex-direction: column-reverse;
      align-items: center;
    }
    .nav {
      margin-right: 56px;
      @include response(767px) {
        margin: 0 0 36px;
      }
      ul {
        flex-direction: column;
        align-items: flex-start;
        @include response(767px) {
          align-items: center;
        }
      }
    }
    .contacts {
      align-items: flex-start;
      @include response(767px) {
        align-items: center;
        margin-bottom: 38px;
      }
      &__phone {
        @include response(767px) {
          margin-bottom: 8px;
        }
      }
      &__email {
        margin-bottom: 6px;
      }
    }
  }
}

.footer-logo {
  p {
    margin: 0 0 10px;
    > img {
      display: block;
      @include response(767px) {
        margin: 0 auto;
      }
    }
  }
  strong {
    font-size: 30px;
    line-height: 36px;
    font-weight: bold;
    color: $white;
    text-align: left;
  }
}